import { CdkStep } from '@angular/cdk/stepper';
import { Directive, Input, TemplateRef } from '@angular/core';
import { C8yStepper } from '@c8y/ngx-components';

@Directive({ selector: '[customStep]' })
export class CustomStep {
  @Input('customStep') label: string;
  @Input('customStepCompleted') completed: string;
  @Input('customStepButtonsDisabled') buttonsDisabled: boolean = false;
  constructor(public templateRef: TemplateRef<any>) {}
  @Input('customStepOnNext') onNext: (event: { stepper: C8yStepper; step: CdkStep }) => void = ({
    stepper,
    step
  }) => {
    // steps without own `onNext` handler, e.g. preview steps need to mark themselves as `completed`,
    // otherwise stepper will not allow to move forth from them as soon as the user navigates back
    // and the `c8y-stepper-buttons` component marks the step as incomplete.
    step.completed = true;
    stepper.next();
  };
}
