import { CdkStep, StepperSelectionEvent } from '@angular/cdk/stepper';
import {
  Component,
  ContentChildren,
  EventEmitter,
  OnDestroy,
  Output,
  QueryList,
  ViewChild
} from '@angular/core';
import { FormGroup } from '@angular/forms';
import { AlertService, C8yStepper, gettext, ModalService, Status } from '@c8y/ngx-components';
import { get } from 'lodash-es';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { OperationDetailsComponent } from '@c8y/ngx-components/operations/details';
import { OperationSchedule } from '@c8y/ngx-components/operations/bulk-operation-scheduler';
import { CustomStep } from './custom-step.directive';
import {
  OperationDetails,
  BulkOperationType,
  BulkOperationsService
} from '@c8y/ngx-components/operations/bulk-operations-service';

@Component({
  selector: 'c8y-bulk-operation-stepper',
  templateUrl: 'bulk-operation-stepper.component.html'
})
export class BulkOperationStepper implements OnDestroy {
  @Output() selectionChange: EventEmitter<StepperSelectionEvent> = new EventEmitter();
  @ContentChildren(CustomStep) customSteps: QueryList<CustomStep>;
  @ViewChild(C8yStepper, { static: false })
  stepper: C8yStepper;
  @ViewChild(OperationDetailsComponent, { static: false })
  operationDetailsComponent: OperationDetailsComponent;

  steps: CustomStep[] = [];
  showStepper: boolean = false;
  showButtons: boolean = false;
  pendingStatus: boolean;
  stepperButtonsLabels = { custom: gettext('Schedule bulk operation') };
  deviceTypes$: Observable<string[]>;
  deviceQueryString: string;
  bulkOperationType: BulkOperationType;
  scheduleData: OperationSchedule;
  operationDetailsForm: FormGroup;
  operationDetails: OperationDetails;
  retrieveOperationDetails: () => OperationDetails | Promise<OperationDetails>;

  private deviceTypesSubject$: Subject<string[]> = new Subject();
  private endSubscriptions: Subject<void> = new Subject();

  constructor(
    private bulkOperationService: BulkOperationsService,
    private modal: ModalService,
    private alert: AlertService
  ) {
    this.deviceTypes$ = this.deviceTypesSubject$.asObservable();
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      // wait for the next event loop turn as `steps` has already been checked in this CD cycle
      this.steps = this.customSteps.toArray();
      this.showStepper = true;
      setTimeout(() => {
        // postpone rendering of buttons for custom steps to the point where custom steps have already been rendered
        this.showButtons = true;
        if (this.stepper) {
          this.stepper.selectionChange.pipe(takeUntil(this.endSubscriptions)).subscribe(event => {
            this.selectionChange.next(event);
          });
          this.operationDetailsForm = this.operationDetailsComponent.fgOperationDescription;
        }
      });
    });
  }

  changeDeviceTypes(deviceTypes: string | string[]) {
    if (deviceTypes) {
      this.deviceTypesSubject$.next(Array.isArray(deviceTypes) ? deviceTypes : [deviceTypes]);
    } else {
      this.deviceTypesSubject$.next([]);
    }
  }

  async confirmDeviceSelection($event: { stepper: C8yStepper; step: CdkStep }) {
    if (!this.deviceQueryString) {
      try {
        await this.modal.confirm(
          gettext('All devices selected'),
          gettext(
            'You are about to schedule the bulk operation to be executed for all devices. Do you want to proceed?'
          ),
          Status.WARNING,
          { ok: gettext('Schedule for all devices'), cancel: gettext('Cancel and select devices') }
        );
        $event.step.completed = true;
        $event.stepper.next();
        this.operationDetails = this.retrieveOperationDetails
          ? await this.retrieveOperationDetails()
          : undefined;
      } catch (ex) {
        // Intentionally empty
      }
    } else {
      $event.step.completed = true;
      $event.stepper.next();
      this.operationDetails = this.retrieveOperationDetails
        ? await this.retrieveOperationDetails()
        : undefined;
    }

    this.bulkOperationType = this.bulkOperationService.retrieveBulkOperationType(
      get(this.operationDetails, 'prototype')
    );
    if (
      this.operationDetailsForm &&
      get(this.operationDetailsForm, 'controls.description.pristine') &&
      this.operationDetails
    ) {
      this.operationDetailsForm.patchValue({
        description: get(this.operationDetails, 'prototype.description')
      });
    }
  }

  cancel() {
    this.close();
  }

  async scheduleBulkOperation() {
    this.pendingStatus = true;

    try {
      this.operationDetails.prototype.description = get(
        this.operationDetailsForm,
        'controls.description.value'
      );
      this.operationDetails.note = get(this.operationDetailsForm, 'controls.note.value');
      this.operationDetails.schedule = get(this.operationDetailsForm, 'controls.schedule.value');

      await this.bulkOperationService.scheduleBulkOperation(
        this.deviceQueryString,
        this.operationDetails
      );
      this.alert.success(gettext('New bulk operation scheduled.'));
      this.close();
    } catch (ex) {
      this.alert.addServerFailure(ex);
    }

    this.pendingStatus = false;
  }

  ngOnDestroy(): void {
    this.endSubscriptions.next();
    this.endSubscriptions.complete();
  }

  private close() {
    this.stepper.reset();
    this.bulkOperationService.returnToBulkOperationOverview();
  }
}
